import React from 'react'
import IcDefault from '../images/ic-arrow-drop-down.svg'

export default function Button(props) {
  const { type, text, icon } = props
  if (type === "primary") {
    return (
      <div className="flex">
        <div className="inline-flex items-center flex-initial px-5 py-2 rounded-lg bg-primary hover:bg-primary-lighter">
          <img className="pr-2" src={icon || IcDefault} alt={text} />
          <p className="text-base font-semibold tracking-wide text-white">{text}</p>
        </div>
      </div>
    )
  } else {
    return (
      <div className="flex">
        <div className="inline-flex items-center flex-initial px-5 py-2 rounded-lg bg-secondary hover:bg-secondary-lighter">
          <img className="pr-2" src={icon || IcDefault} alt={text} />
          <p className="text-base font-semibold tracking-wide text-white">{text}</p>
        </div>
      </div>
    )
  }
}

Button.defaultProps = {
  type: "primary",
  text: "Button"
}
