import React from 'react'
import Button from './Button'
import IcDoc from '../images/ic-doc.svg'
import IcDevices from '../images/ic-devices.svg'
import { Link } from 'react-router-dom'

export default function AboutMe() {
  return (
      <div className="flex flex-col-reverse py-10 md:py-16 md:flex-row">
        <div data-aos="zoom-in-right" className="flex flex-col w-full md:w-6/12">
          <h1 className="pt-4 text-3xl font-semibold leading-7 text-white md:text-4xl md:pt-0 md:leading-none">Jirayu Pharang</h1>
          <p className="pt-1 text-xl font-hairline text-white text-gray-500 md:text-2xl md:pt-2">Software Engineer</p>
          <p className="pt-1 pb-2 text-base text-gray-500 whitespace-pre-wrap md:pt-2">Hi, my name is Jirayu Pharang and I'm a software engineer. Welcome to my personal website. Want to know how I may help your project? Check out my project portfolio and online resume.</p>
          <div className="grid hidden grid-flow-row grid-cols-1 gap-2 px-4 py-2 mx-auto md:grid-cols-2">
            <Link to="/portfolio">
              <Button text={"View Portfolio"} icon={IcDoc} />
            </Link>
            <Link to="/resume">
              <Button text={"View Resume"} icon={IcDevices} type={"secondary"} />
            </Link>
          </div>
        </div>
        <div data-aos="zoom-in-left" className="flex flex-row justify-end w-full md:w-6/12">
          <img className="object-contain w-full md:w-10/12 " src={require(`../images/profile-lg.jpg`)} alt="profile" />
        </div>
      </div>
  )
}
